<script setup>
import { ref } from 'vue';
import { RouterLink } from 'vue-router'
import { useRoute } from 'vue-router';
import IconAddGroup from "../components/icons/IconAddGroup.vue";
import IconAva from "../components/icons/IconAva.vue";
import { useContatcsStore } from "@/stores/contatcs";

const isGroupShow = ref(false);

const contactsStore = useContatcsStore();

const route = useRoute();

const id = +route.params.id

const contactSolo = ref(contactsStore?.contactUsers[id])

const uploadFile = (event) => {
    const selectedFile = event.target.files[0];
    const reader = new FileReader();

    reader.onload = () => {
        contactSolo.value.ava = reader.result;
    };

    reader.onerror = () => {
        contactSolo.value.ava = null;
    };

    reader.readAsDataURL(selectedFile);
};

</script>

<template>
    <main>
        <section class="edit">
            <div class="container">
                <h2 class="edit__title title">Edit</h2>
                <form class="edit__content">
                    <label class="edit__content-labelAva">
                        <img v-if="contactSolo.ava" :src="contactSolo.ava" alt="" class="edit__content-ava">
                        <IconAva widthAva="100" heightAva="100" v-else />
                        <input accept="image/jpeg,image/png,image/gif" type="file" class="edit__content-file"
                            @change="uploadFile" />
                    </label>
                    <label class="edit__content-label">
                        Name
                        <input type="text" class="edit__content-inp" placeholder="Name" v-model="contactSolo.name">
                    </label>
                    <label class="edit__content-label">
                        Phone
                        <input type="tel" class="edit__content-inp" placeholder="Phone" v-model="contactSolo.phone">
                    </label>
                    <label class="edit__content-label">
                        Email
                        <input type="email" class="edit__content-inp" placeholder="Email" v-model="contactSolo.email">
                    </label>
                    <label class="edit__content-label">
                        {{ contactSolo.tag ? "Group: " + contactSolo.tag : "Not grouped" }}
                    </label>
                    <span class="group" @click="isGroupShow = true">
                        <IconAddGroup />
                    </span>
                    <ul class="modal-group" :class="{ active: isGroupShow }">
                        <li class="modal-group__item" data-type="Family"
                            @click="isGroupShow = false, contactSolo.tag = 'Family'">Family</li>
                        <li class="modal-group__item" data-type="My contacts"
                            @click="isGroupShow = false, contactSolo.tag = 'My contacts'">My contacts</li>
                        <li class="modal-group__item" data-type="Friends"
                            @click="isGroupShow = false, contactSolo.tag = 'Friends'">Friends</li>
                        <li class="modal-group__item" data-type="Colleagues"
                            @click="isGroupShow = false, contactSolo.tag = 'Colleagues'">Colleagues</li>
                    </ul>
                    <div class="edit__content-btns">
                        <RouterLink to="/" class="edit__content-back btn">Back</RouterLink>
                    </div>
                </form>
            </div>
        </section>
    </main>
</template>

<style></style>